"use client";

import { LogOut, User } from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "./ui/avatar";
import { Button } from "./ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "./ui/dropdown-menu";
import { SidebarTrigger } from "./ui/sidebar";
import { signOut } from "next-auth/react";
import Link from "next/link";
import { requireAuth } from "@/helpers/auth-guard";

export default function Navbar() {
  const user = requireAuth();
  return (
    <>
      <div className="h-16 border-b border-bran-secondary flex justify-between items-center px-4">
        <div className="bg-  flex justify-center items-center">
          {" "}
          <SidebarTrigger className="ml-" />
        </div>
        <div className="">
          {" "}
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" className="relative h-8 w-8 rounded-full">
                <Avatar className="h-8 w-8">
                  <AvatarImage src={"/avatar.png"} />
                  <AvatarFallback className="bg-primary text-primary-foreground">
                    A
                  </AvatarFallback>
                </Avatar>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent
              className="w-56 bg-popover border-bran-secondary p-2"
              align="end"
              forceMount
            >
              <DropdownMenuLabel className="font-normal">
                <div className="flex flex-col space-y-1">
                  <p className="text-sm font-medium leading-none">
                    {user!.first_name}
                  </p>
                  <p className="text-xs leading-none text-muted-foreground">
                    {user!.email}
                  </p>
                </div>
              </DropdownMenuLabel>
              <DropdownMenuSeparator />
              <DropdownMenuItem
                className="hover:to-brand-primary font-medium py-3"
                asChild
              >
                <Link className="pl-6" href={"/profile"}>
                  Profile
                </Link>
              </DropdownMenuItem>
              <DropdownMenuItem
                onClick={() => signOut({ callbackUrl: "/" })} //  no form needed
                className="py-2 cursor-pointer"
              >
                <LogOut className="mr-2" />
                Sign Out
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>
    </>
  );
}
